<template>
  <div>
    <div class="mt-5 col-md-12">
      <b-row class="no-print">
        <b-col cols="12" class="text-right">
          <b-button id="btn77181" type="submit" size="sm" variant="warning" @click="printPage()"><i class="fa fa-print"></i> Print</b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <b-col cols="12" class="text-center" v-html="jsonData.waiver"></b-col>

          <hr>
          <b-row>
            <b-col cols="4" class="text-left"><h3>INFORMATION</h3></b-col>
          </b-row>
          <b-row>
            <b-col sm="6" class="text-sm-right"><b>First Name: </b></b-col>
            <b-col sm="6">{{ jsonData.info.first_name }}</b-col>
          </b-row>
          <b-row>
            <b-col sm="6" class="text-sm-right"><b>Last Name: </b></b-col>
            <b-col sm="6">{{ jsonData.info.last_name }}</b-col>
          </b-row>
          <b-row>
            <b-col sm="6" class="text-sm-right"><b>Phone Number: </b></b-col>
            <b-col sm="6">{{ jsonData.info.phone }}</b-col>
          </b-row>
          <b-row>
            <b-col sm="6" class="text-sm-right"><b>Email Address: </b></b-col>
            <b-col sm="6">{{ jsonData.info.email }}</b-col>
          </b-row>
          <b-row>
            <b-col sm="6" class="text-sm-right"><b>Date of Birth: </b></b-col>
            <b-col sm="6">{{ jsonData.info.dob }}</b-col>
          </b-row>


          <div v-if="jsonData['minors'].length > 0">
            <hr>
            <b-row>
              <b-col cols="4" class="text-left"><h3>MINORS</h3></b-col>
            </b-row>
            <b-row class="mb-1 mt-2" v-for="item in jsonData['minors']">
              <b-col sm="2" class="text-sm-right"><b>Name: </b></b-col>
              <b-col sm="3">{{ item['first_name'] }} {{ item['last_name'] }}</b-col>
              <b-col sm="2" class="text-sm-right"><b>Relationship: </b></b-col>
              <b-col sm="2">{{ item['relationship'] }}</b-col>
              <b-col sm="1" class="text-sm-right"><b>DOB: </b></b-col>
              <b-col sm="2">{{ item['dob'] }}</b-col>
            </b-row>
          </div>

          <div v-if="jsonData['questions'].length > 0">
            <hr>
            <b-row>
              <b-col cols="4" class="text-left"><h3>QUESTIONS</h3></b-col>
            </b-row>
            <b-row class="mb-1 mt-2" v-for="item in jsonData['questions']">
              <b-col sm="6" class="text-sm-right"><b>{{ item.question }}</b></b-col>
              <b-col sm="6">{{ item.answer }}</b-col>
            </b-row>
          </div>

          <hr>
          <h3><i class="fa fa-pencil text-primary mr-2"></i> SIGNATURE: </h3>
          <b-row class="mb-1 mt-2" v-if="jsonData['signature']['manual_accept']">
            <b-col sm="6" class="text-sm-right"><b>E-Signature: </b></b-col>
            <b-col sm="6">{{ jsonData['signature']['manual_accept'] }}</b-col>
          </b-row>
          <b-row class="mb-1 mt-2" v-if="jsonData['signature']['manual_accept']">
            <b-col sm="6" class="text-sm-right"><b>Signature: </b></b-col>
            <b-col sm="6">{{ jsonData['signature']['manual_signature'] }}</b-col>
          </b-row>
          <b-row class="mb-1 mt-2" v-if="!jsonData['signature']['manual_accept']">
            <img width="100%" :src="jsonData['signature']['signature_png']">
          </b-row>


        </b-col>
      </b-row>

    </div>
  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'WaiverPrint',
    data () {
      return {
        signatureID: null,
        signature: null,
        jsonData: null,
        loading: false
      }
    },
    mounted: function () {
      this.signatureID = this.$route.params.id
      this.getWaiver()
    },
    methods: {
      getWaiver () {
        this.loading = true
        rangepro.get_secure('store/waiver/signature/' + this.signatureID, this.handleWaiverResponse, 'InternalError')
      },
      handleWaiverResponse (data, success) {
        if (success) {
          this.signature = data.signature
          this.jsonData = JSON.parse(this.signature['waiver_data'])
          this.loading = false
        } else {
          this.loading = false
        }
      },
      printPage () {
        window.print()
      }
    }
  }
</script>

<style scoped>
  @media print
  {
    .no-print, .no-print *
    {
      display: none !important;
    }
  }
</style>



