<template>
  <div>
    <b-row>
      <b-col cols="6">
        <h1 class="pull-left text-primary"><i class="fa fa-th-large text-danger"></i> {{ getTitle() }}</h1>
      </b-col>
      <b-col></b-col>
      <b-col cols="2">
        <router-link :to="{ name: 'Kiosk Dashboard', params: { id: kioskID }}"><b-button id="btn810" type="submit" class="btn btn-md pull-right" variant="warning"><i class="fa fa-home"></i> Home</b-button></router-link>
      </b-col>
    </b-row>
    <div class="mt-5 col-md-12">

      <b-row v-if="stage  === 'start'">
        <b-col cols="12">

          <b-form-group
            label="Customer Name"
            label-for="customerName"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="customerName" type="text" v-model="customer_info.name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.name.required">Customer Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Phone Number"
            label-for="customerPhone"
            :label-cols="2"
            :horizontal="true">
            <masked-input
              type="tel"
              name="phone"
              class="form-control"
              v-model="customer_info.phone"
              :mask="['(' , /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]"
              :guide="true"
              placeholderChar="#" @focus="clearReservationPhone()">
            </masked-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.phone.required">Phone is Required</span>
          </b-form-group>

          <b-form-group
            :label="'Number of ' + range.terminology"
            label-for="numLanes"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="numLanes"
                           :plain="true"
                           :options="numLanesOption"
                           v-model="customer_info.lanes">
            </b-form-select>
          </b-form-group>

          <b-form-group
            label="Session"
            label-for="sessionLength"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="sessionLength"
                           :plain="true"
                           :options="sessionOptions"
                           v-model="customer_info.session">
            </b-form-select>
          </b-form-group>

        </b-col>

      </b-row>

      <b-row v-if="stage  === 'submitted'">
        <b-col cols="12" class="text-center">
          <h2><i class="fa fa-check-circle text-success"></i> You Have Been Added to the Waiting List</h2>
        </b-col>
      </b-row>


      <b-row class="mt-4" v-if="stage === 'start'">
        <b-col cols="6"></b-col>
        <b-col cols="6">
          <b-button id="btn811" type="submit" class="btn btn-md pull-right" variant="success" @click="setStage('submit')" :disabled="$v.customer_info.$invalid"><i class="fa fa-arrow-right"></i> NEXT</b-button>
        </b-col>
      </b-row>

    </div>
  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import router from '@/router'
  import MaskedInput from 'vue-text-mask'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'WaiverForm',
    components: {
      MaskedInput
    },
    data () {
      return {
        kioskID: null,
        rangeID: null,
        loading: false,
        stage: 'start',
        range: null,
        customer_info: {
          lanes: 1,
          name: null,
          phone: '',
          session: null
        },
        numLanesOption: [],
        sessionOptions: []
      }
    },
    mounted: function () {
      this.kioskID = this.$route.params.id
      this.rangeID = this.$route.params.formid
      this.getRange()
    },
    methods: {
      getRange () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID, this.handleRangeResponse, 'InternalError')
      },
      saveWaitingList () {
        this.loading = true
        var waitingList = {
          waiting_list: {
            client_name: this.customer_info.name,
            lanes: this.customer_info.lanes,
            phone: this.customer_info.phone,
            checked_in: true,
            check_in_time: new Date().valueOf() / 1000,
            session_length: this.customer_info.session,
            session_type: {name: 'self-service', legend: 'ss'}
          }
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/waiting_list', waitingList, this.handleWaitingListAction, 'InternalError', null)
      },
      handleRangeResponse (data, success) {
        if (success) {
          this.range = data.range
          this.setupLanes(this.range['lanes'])
          this.setupSessions(this.range['session_lengths'])
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleWaitingListAction (data, success) {
        if (success) {
          this.setStage('submitted')
        }
        this.loading = false
      },
      setupLanes (lanes) {
        this.numLanesOption = []
        var x = 1
        while (x <= lanes) {
          this.numLanesOption.push({
            text: x, value: x
          })
          x++
        }
      },
      setupSessions (sessions) {
        this.sessionOptions = []
        for (var index in sessions) {
          if (sessions[index]['public']) {
            this.sessionOptions.push({
              text: sessions[index]['name'],
              value: sessions[index]
            })
            this.customer_info.session = sessions[index]
          }
        }
      },
      getTitle () {
        if (this.stage === 'start') {
          return 'INFORMATION'
        } else if (this.stage === 'submit') {
          return 'ASSIGNING...'
        } else if (this.stage === 'submitted') {
          return 'ADDED TO WAITING LIST'
        }
      },
      clearPhone () {
        if (this.customer_info.phone === '(###) ###-####') {
          this.customer_info.phone = ''
        }
      },
      setStage (stage) {
        if (stage === 'start') {
          this.stage = stage
        } else if (stage === 'submit') {
          this.saveWaitingList()
        } else if (stage === 'submitted') {
          var ID = this.kioskID
          setTimeout(function () { router.push({name: 'Kiosk Dashboard', params: {id: ID}}) }, 5000)
          this.stage = stage
        } else {
          this.stage = stage
        }
      }
    },
    validations: {
      customer_info: {
        name: {
          required
        },
        phone: {
          required
        }
      }
    }
  }
</script>

<style scoped>
</style>



