<template>
  <div class="col-12">

    <b-card class="bg-white p-0" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Search</strong> Range History
        <span class="pull-right mt-2">
          <router-link :to="{ name: 'Store Range Dashboard', params: { id: rangeID }}"><b-button id="btn77" type="submit" size="sm" variant="primary"><i class="fa fa-th"></i> Dashboard</b-button></router-link>
        </span>
      </div>
      <div ref="card" class="text-dark">

        <b-form-group
          label="Start Date"
          label-for="startDate"
          :label-cols="3"
          :horizontal="true">
          <b-form-input id="searchDate" type="date" v-model="start_date"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.start_date.required">Start Date is Required</span>
        </b-form-group>

        <b-form-group
          label="End Date"
          label-for="endDate"
          :label-cols="3"
          :horizontal="true">
          <b-form-input id="searchDate" type="date" v-model="end_date"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.end_date.required">End Date is Required</span>
        </b-form-group>

        <b-btn id="btn731" class="mt-3" variant="success" block @click="getCharts()" :disabled="$v.start_date.$invalid || $v.end_date.$invalid || loading_tu || loading_spm || loading_hpm || loading_st || loading_sl || loading_mem"> Get Charts</b-btn>

      </div>
    </b-card>

    <b-card class="bg-white col-md-12">
        <h2 class="text-center">Target Usage <img class="ml-3" src="@/assets/loading.gif" v-if="loading_tu === true"></h2>
        <target-chart
          ref="targetUsageChart"
          :chart-data="target_chart_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="250">
        </target-chart>
    </b-card>

    <b-card-group deck class="mb-4">
      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Sessions Per Month <img class="ml-3" src="@/assets/loading.gif" v-if="loading_spm === true"></h2>
        <sessions-per-month-chart
          ref="sessionsPerMonthChart"
          :chart-data="sessions_per_month_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="300">
        </sessions-per-month-chart>
      </b-card>

      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Hours Per Month <img class="ml-3" src="@/assets/loading.gif" v-if="loading_hpm === true"></h2>
        <hours-per-month-chart
          ref="hoursPerMonthChart"
          :chart-data="hours_per_month_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="300">
        </hours-per-month-chart>
      </b-card>
    </b-card-group>

    <b-card-group deck class="mb-4">

      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Session Lengths <img class="ml-3" src="@/assets/loading.gif" v-if="loading_sl === true"></h2>
        <session-lengths-chart
          ref="sessionLengthsChart"
          :chart-data="session_lengths_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="400">
        </session-lengths-chart>
      </b-card>

      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Session Types <img class="ml-3" src="@/assets/loading.gif" v-if="loading_st === true"></h2>
        <session-lengths-chart
          ref="sessionTypesChart"
          :chart-data="session_types_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="400">
        </session-lengths-chart>
      </b-card>

    </b-card-group>

    <b-card-group deck>

      <b-card class="bg-white col-md-12">
        <h2 class="text-center">Member Usage <img class="ml-3" src="@/assets/loading.gif" v-if="loading_mem === true"></h2>
        <target-chart
          ref="memberMinutesChart"
          :chart-data="member_minutes_chart_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="250">
        </target-chart>
      </b-card>

    </b-card-group>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required } from 'vuelidate/lib/validators'
  import TargetChart from './RangeTargetUsageChart'
  import SessionsPerMonthChart from './RangeSessionsPerMonthChart'
  import HoursPerMonthChart from './RangeHoursPerMonthChart'
  import SessionLengthsChart from './RangeSessionLengthsChart'
  import SessionTypesChart from './RangeSessionTypesChart'
  import MemberMinutesChart from './RangeMemberMinutesChart'

  export default {
    name: 'RangeHistory',
    components: {
      TargetChart,
      SessionsPerMonthChart,
      HoursPerMonthChart,
      SessionLengthsChart,
      SessionTypesChart,
      MemberMinutesChart
    },
    data () {
      return {
        rangeID: null,
        start_date: null,
        end_date: null,
        loading_tu: false,
        loading_spm: false,
        loading_hpm: false,
        loading_sl: false,
        loading_st: false,
        loading_mem: false,
        target_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Usage Hours',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        sessions_per_month_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        hours_per_month_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        session_lengths_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        session_types_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        member_minutes_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Members',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        }
      }
    },
    mounted: function () {
      this.rangeID = this.$route.params.id
    },
    methods: {
      getCharts () {
        this.getTargetUsage()
        this.getHoursPerMonth()
        this.getSessionLengths()
        this.getSessionTypes()
        this.getSessionsPerMonth()
        this.getMemberMinutes()
      },
      getChart (chartName, callback) {
        this.loading = true
        var chartConfig = {
          chart: {
            name: chartName,
            start: formats.format_date_to_ts(formats.format_date(this.start_date)),
            end: formats.format_date_to_ts(formats.format_date(this.end_date))
          }
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/chart', chartConfig, callback, 'SearchRangeHistory', null)
      },
      handleTargetChartResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: 'Usage Hours',
            backgroundColor: '#F64F4F',
            data: data.data
          }
          this.target_chart_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.targetUsageChart.updateChart()
          this.loading_tu = false
        } else {
          this.loading_tu = false
        }
      },
      handleSessionsPerMonthResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Sessions',
            backgroundColor: '#1c6eb5',
            data: data.data
          }
          this.sessions_per_month_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.sessionsPerMonthChart.updateChart()
          this.loading_spm = false
        } else {
          this.loading_spm = false
        }
      },
      handleHoursPerMonthResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Hours',
            backgroundColor: '#0AB25E',
            data: data.data
          }
          this.hours_per_month_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.hoursPerMonthChart.updateChart()
          this.loading_hpm = false
        } else {
          this.loading_hpm = false
        }
      },
      handleSessionLengthsResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Sessions',
            backgroundColor: this.getPieChartBackgrounds(data.data.length),
            data: data.data
          }
          this.session_lengths_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.sessionLengthsChart.updateChart()
          this.loading_sl = false
        } else {
          this.loading_sl = false
        }
      },
      handleSessionTypesResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Sessions',
            backgroundColor: this.getPieChartBackgrounds(data.data.length),
            data: data.data
          }
          this.session_types_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.sessionTypesChart.updateChart()
          this.loading_st = false
        } else {
          this.loading_st = false
        }
      },
      handleMemberMinutesChartResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: 'Members',
            backgroundColor: '#F64F4F',
            data: data.data
          }
          this.member_minutes_chart_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.memberMinutesChart.updateChart()
          this.loading_mem = false
        } else {
          this.loading_mem = false
        }
      },
      getTargetUsage () {
        this.loading_tu = true
        this.getChart('target_usage', this.handleTargetChartResponse)
      },
      getSessionsPerMonth () {
        this.loading_spm = true
        this.getChart('sessions_per_month', this.handleSessionsPerMonthResponse)
      },
      getHoursPerMonth () {
        this.loading_hpm = true
        this.getChart('hours_per_month', this.handleHoursPerMonthResponse)
      },
      getSessionLengths () {
        this.loading_sl = true
        this.getChart('session_lengths', this.handleSessionLengthsResponse)
      },
      getSessionTypes () {
        this.loading_st = true
        this.getChart('session_types', this.handleSessionTypesResponse)
      },
      getMemberMinutes () {
        this.loading_mem = true
        this.getChart('member_minutes', this.handleMemberMinutesChartResponse)
      },
      getPieChartBackgrounds (arrayLength) {
        var baseChoices = [
          'rgba(65, 184, 131, .8)',
          'rgba(228, 102, 81, .8)',
          'rgba(0, 216, 255, .8)',
          'rgba(155, 89, 182, .8)',
          'rgba(209, 169, 249, .8)',
          'rgba(169, 209, 249, .8)',
          'rgba(200, 13, 107, .8)',
          'rgba(200, 200, 13, .7)'
        ]
        return baseChoices.slice(0, arrayLength)
      }
    },
    validations: {
      start_date: {
        required
      },
      end_date: {
        required
      }
    }
  }
</script>

<style scoped>

</style>



