              </span>
                  <span :id="'lane_selection' + item.lane" :class="{button_selected: (selectedLanes.indexOf(item.lane) != -1)}" class="btn btn-circle lane_selection button_notselected"><i class="fa fa-check"></i></span>
                  <div class="client-info">
