      <b-row class="mt-4" v-if="stage === 'lanes'">
        <b-col cols="6">
          <b-button id="btn814" type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('start')"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button id="btn815" type="submit" class="mr-1 ml-1 btn btn-md pull-right" variant="success" @click="setStage('info')" :disabled="selectedLanes < 1"><i class="fa fa-arrow-right"></i> NEXT</b-button>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'info'">
        <b-col cols="6">
          <b-button id="btn816" type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('lanes')"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button id="btn817" type="submit" class="mr-1 ml-1 btn btn-md pull-right" variant="success" @click="setStage('submit')" :disabled="$v.customer_info.name.$invalid"><i class="fa fa-check"></i> ASSIGN</b-button>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'waiting_list'">
        <b-col cols="6">
          <b-button id="btn818" type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('start')"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button id="btn819" type="submit" class="mr-1 ml-1 btn btn-md pull-right" variant="success" @click="setStage('submit_waiting_list')" :disabled="$v.customer_info.$invalid"><i class="fa fa-plus"></i> ADD</b-button>
        </b-col>
