</template>

<script>
  import router from '@/router'
  import store from '@/store'
  import rangepro from '@/services/rangepro'
  import dashboard from '@/services/dashboard'
  import MaskedInput from 'vue-text-mask'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'WaiverForm',
    components: {
      MaskedInput
    },
    data () {
      return {
        kioskID: null,
        rangeID: null,
        config: null,
        loading: false,
        stage: 'start',
        mode: 'vert',
        range: null,
        customer_info: {
          lanes: 1,
          preference: 'first_available',
          name: null,
          phone: '',
          session: null
        },
        numLanesOption: [],
        selectedLanes: [],
        sessions: [],
        sessionsOrdered: {},
        sessionsFirst: [],
        sessionsSecond: [],
        splitLanes: false,
        splitColumns: 'col-md-12',
        splitFirstSet: [],
        splitSecondSet: [],
        open_lanes: 0
      }
    },
    mounted: function () {
      this.kioskID = this.$route.params.id
      this.rangeID = this.$route.params.formid
      this.customer_info.name = store.getters.getWaiverCustomer
      this.customer_info.phone = store.getters.getWaiverPhone
      this.getRange()
      this.getKiosk()
    },
    methods: {
      getRange () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID, this.handleRangeResponse, 'InternalError')
      },
      getRangeSessions () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID + '/sessions', this.handleSessionsResponse, 'InternalError')
      },
      getKiosk () {
        rangepro.get_secure('store/kiosk/' + this.kioskID, this.handleKioskResponse, 'GetStoreKiosks')
      },
      handleKioskResponse (data, success) {
        if (success) {
          this.config = data.kiosk
          this.loading = false
        } else {
          this.loading = false
        }
      },
      addSessions () {
        this.loading = true
        if (this.selectedLanes.length > 0) {
          var sessions = {
            session: {
              lanes: this.selectedLanes,
              data: {
                client_name: this.customer_info.name,
                source: 'SELF-SERVICE',
                session_length: this.customer_info.session,
                session_type: {name: 'self-service', legend: 'ss'}
              }
            }
          }
          rangepro.post_secure('store/range/' + this.rangeID + '/sessions/bulk', sessions, this.handleBulkUpdateResponse, 'InternalError', null)
        }
      },
      saveWaitingList () {
        this.loading = true
        var waitingList = {
          waiting_list: {
            client_name: this.customer_info.name,
            lanes: this.customer_info.lanes,
            phone: this.customer_info.phone,
            checked_in: true,
            check_in_time: new Date().valueOf() / 1000,
            auto_assignment: true,
            preference: this.customer_info.preference,
            session_length: this.customer_info.session,
            session_type: {name: 'self-service', legend: 'ss'}
          }
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/waiting_list', waitingList, this.handleWaitingListAction, 'InternalError', null)
      },
      handleRangeResponse (data, success) {
        if (success) {
          this.range = data.range
          this.setupLanes(this.range['lanes'])
          this.setupPPU()
          this.getRangeSessions()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSessionsResponse (data, success) {
        if (success) {
          // Set sessions and clear out lane split.
          this.sessions = data.sessions
          this.splitFirstSet = []
          this.splitSecondSet = []
          // Process lane segments being called sets in dashboard.  Determine layout then process
          // the lanes returned and display.
          this.splitLanes = dashboard.check_split(this.range.lanes)
          var countLanes = 1
          // If lanes are greater than 8 then split between two sets.
          if (this.splitLanes) {
            // Get the lane number as the split point.
            let splitLane = dashboard.split_lane(this.range.lanes)
            this.splitColumns = 'col-md-6'
            // Process each set left and right.
            while (countLanes <= splitLane) {
              this.splitFirstSet.push(countLanes)
              countLanes++
            }
            while (countLanes <= this.range.lanes) {
              this.splitSecondSet.push(countLanes)
              countLanes++
            }
          } else {
            // If lanes are less than 8, just display in one set.
            countLanes = 1
            while (countLanes <= this.range.lanes) {
              this.splitFirstSet.push(countLanes)
              countLanes++
            }
          }
          this.loading = false
          // Process sessions into their respective sets for display.
          this.processSessions()
        } else {
          this.loading = false
        }
      },
      handleBulkUpdateResponse (data, success) {
        if (success) {
          this.setStage('assignment')
        }
        this.loading = false
      },
      handleWaitingListAction (data, success) {
        if (success) {
          this.setStage('waiting_list_assigned')
        }
        this.loading = false
      },
      processSessions () {
        // Process sessions and configure presentation of lane data.
        this.sessionsOrdered = dashboard.order_sessions(this.sessions)
        var processed = dashboard.setup_sessions(this.range, this.sessionsOrdered, this.splitFirstSet, this.splitSecondSet)
        this.sessionsFirst = processed.completeFirstSet
        this.sessionsSecond = processed.completeSecondSet
        // Determine available lanes
        this.open_lanes = processed.open
      },
      addRemoveSelectedLane (lane) {
        if (lane in this.sessionsOrdered) {
          return null
        }
        if (this.selectedLanes.indexOf(lane) > -1) {
          var index = this.selectedLanes.indexOf(lane)
          this.selectedLanes.splice(index, 1)
        } else {
          if (this.selectedLanes.length >= this.customer_info.lanes) {
            return null
          }
          this.selectedLanes.push(lane)
        }
      },
      setupPPU () {
        for (var index in this.range['session_lengths']) {
          if (this.range['session_lengths'][index]['pay_per_use']) {
            this.customer_info.session = this.range['session_lengths'][index]
          }
        }
      },
      setupLanes (lanes) {
        this.numLanesOption = []
        var x = 1
        while (x <= lanes) {
          this.numLanesOption.push({
            text: x, value: x
          })
          x++
        }
      },
      determineAvailability () {
        // determine if enough lanes and align to preference
        if (this.customer_info.lanes > this.open_lanes) {
          return false
        }
        if (this.customer_info.lanes <= this.open_lanes) {
          // check preference
          if (this.customer_info.preference === 'first_available') {
            // if first available then just allow them to pick lanes
            return true
          } else {
            // if group together check to see that there are lanes together
            var x = 1
            var lastLane = -5
            var consecutive = 0
            while (x <= this.range.lanes) {
              if (x in this.sessionsOrdered) {
                // if session exists, reset
                lastLane = -5
                consecutive = 0
              } else {
                // if the last last and current lane are
                // equal to 1 then it's a consecutive
                if ((x - lastLane) === 1) {
                  consecutive++
                } else {
                  consecutive = 0
                }
                lastLane = x
              }
              // 1 consecutive = 2 lanes, 2 consecutive = 3 lanes
              if (consecutive >= (this.customer_info.lanes - 1)) {
                return true
              }
              x++
            }
            return false
          }
        }
      },
      getTitle () {
        if (this.stage === 'start') {
          return 'BEGIN ASSIGNMENT'
        } else if (this.stage === 'lanes') {
          return 'SELECT OPEN ' + this.range.terminology.toUpperCase()
        } else if (this.stage === 'info') {
          return 'BASIC INFORMATION'
        } else if (this.stage === 'waiting_list') {
          return 'NO ' + this.range.terminology.toUpperCase() + ' AVAILABLE'
        } else if (this.stage === 'waiting_list_assigned') {
          return 'ADDED TO WAITING LIST'
        } else if (this.stage === 'submit') {
          return 'ASSIGNING...'
        } else if (this.stage === 'submit_waiting_list') {
          return 'SAVING...'
        } else if (this.stage === 'assignment') {
          return 'ASSIGNMENT COMPLETE'
        }
      },
      clearPhone () {
        if (this.customer_info.phone === '(###) ###-####') {
          this.customer_info.phone = ''
        }
      },
      setStage (stage) {
        if (stage === 'start') {
          this.stage = stage
        } else if (stage === 'lanes') {
          if (this.determineAvailability()) {
            this.stage = stage
          } else {
            this.stage = 'waiting_list'
          }
        } else if (stage === 'info') {
          this.stage = stage
        } else if (stage === 'submit') {
          this.addSessions()
          this.stage = stage
        } else if (stage === 'assignment') {
          var aID = this.kioskID
          setTimeout(function () { router.push({name: 'Kiosk Dashboard', params: {id: aID}}) }, 5000)
          this.stage = stage
        } else if (stage === 'submit_waiting_list') {
          this.saveWaitingList()
          this.stage = stage
        } else if (stage === 'waiting_list') {
          this.stage = stage
        } else if (stage === 'waiting_list_assigned') {
          var wlID = this.kioskID
          setTimeout(function () { router.push({name: 'Kiosk Dashboard', params: {id: wlID}}) }, 5000)
          this.stage = stage
        } else {
          this.stage = stage
        }
      }
    },
    validations: {
