import store from '../store'
import axios from 'axios'

export default {

  get_secure: function (url, callbackFunction, errorMsg) {
    callRangePro(url, 'get', true, {}, false, null, callbackFunction, errorMsg, null)
  },
  get_pub_secure: function (url, key, callbackFunction, errorMsg) {
    callRangePro(url, 'get', true, {}, false, null, callbackFunction, errorMsg, null, key)
  },
  get_download_secure: function (url, fileName, callbackFunction, errorMsg) {
    callRangePro(url, 'get', true, {}, false, null, callbackFunction, errorMsg, null, null, true, fileName)
  },
  put_secure: function (url, data, callbackFunction, errorMsg, successMsg) {
    callRangePro(url, 'put', true, data, false, null, callbackFunction, errorMsg, successMsg)
  },
  post_secure: function (url, data, callbackFunction, errorMsg, successMsg) {
    callRangePro(url, 'post', true, data, false, null, callbackFunction, errorMsg, successMsg)
  },
  delete_secure: function (url, id, callbackFunction, errorMsg, successMsg) {
    callRangePro(url + '/' + id, 'delete', true, null, false, null, callbackFunction, errorMsg, successMsg)
  },
  delete_bulk_secure: function (url, data, callbackFunction, errorMsg, successMsg) {
    callRangePro(url, 'delete', true, data, false, null, callbackFunction, errorMsg, successMsg)
  },
  delete_secure_payload: function (url, data, callbackFunction, errorMsg, successMsg) {
    callRangePro(url, 'delete', true, data, false, null, callbackFunction, errorMsg, successMsg)
  }

}

function processErrorMsg (errorMsg) {
  var ERRORS = {
    'InternalError': 'Unexpected Error: Please contact support@range-pro.com.',
    'GetStoreRanges': 'Error: Retrieving/deleting store ranges. Please contact support@range-pro.com.',
    'AddEditStoreRanges': 'Error: Adding/updating store range. Please contact support@range-pro.com.',
    'DeleteRangeSession': 'Error: Deleting range session. Please contact support@range-pro.com.',
    'GetRangeSessions': 'Error: Retrieving range sessions. Please contact support@range-pro.com.',
    'AddRangeSessions': 'Error: Adding range sessions. Please contact support@range-pro.com.',
    'ExtendRangeSessions': 'Error: Extending range sessions. Please contact support@range-pro.com.',
    'MoveRangeSessions': 'Error: Moving range sessions. Please contact support@range-pro.com.',
    'GetWaitingList': 'Error: Retrieving waiting list. Please contact support@range-pro.com',
    'AddWaitingList': 'Error: Adding customer to waiting list. Please contact support@range-pro.com',
    'UpdateWaitingList': 'Error: Updating customer to waiting list. Please contact support@range-pro.com',
    'CheckInWaitingList': 'Error: Checking in customer. Please contact support@range-pro.com',
    'DeleteWaitingList': 'Error: Removing customer from waiting list. Please contact support@range-pro.com',
    'AddRangeReservation': 'Error: Saving customer reservation. Please contact support@range-pro.com',
    'UpdateRangeReservation': 'Error: Updating customer reservation. Please contact support@range-pro.com',
    'MarkReservationUsed': 'Error: Marking reservation as used. Please contact support@range-pro.com',
    'GetReservation': 'Error: Retrieving reservation. Please contact support@range-pro.com',
    'EditReservation': 'Error: Editing reservation. Please contact support@range-pro.com',
    'CheckInReservation': 'Error: Checking in customer. Please contact support@range-pro.com',
    'DeleteReservation': 'Error: Removing reservation. Please contact support@range-pro.com',
    'SearchRangeReservations': 'Error: Searching available reservation times. Please contact support@range-pro.com',
    'SearchRangeHistory': 'Error: Searching range history.  Please contact support@range-pro.com',
    'GetStoreDisplay': 'Error: Retrieving store displays. Please contact support@range-pro.com',
    'AddStoreDisplay': 'Error: Adding store display. Please contact support@range-pro.com',
    'UpdateStoreDisplay': 'Error: Updating store display. Please contact support@range-pro.com',
    'DeleteStoreDisplay': 'Error: Deleting store display. Please contact support@range-pro.com',
    'GetStoreKiosks': 'Error: Retrieving store kiosks. Please contact support@range-pro.com',
    'AddStoreKiosk': 'Error: Adding store kiosk. Please contact support@range-pro.com',
    'UpdateStoreKiosk': 'Error: Updating store kiosk. Please contact support@range-pro.com',
    'DeleteStoreKiosks': 'Error: Deleting store kiosks. Please contact support@range-pro.com',
    'GetStoreActiveMembers': 'Error: Retrieving store active members. Please contact support@range-pro.com.',
    'GetStoreOtherMembers': 'Error: Retrieving store unpaid members. Please contact support@range-pro.com.',
    'GetStoreAllMembers': 'Error: Retrieving store all members. Please contact support@range-pro.com.',
    'GetRefreshMemberships': 'Error: Refreshing store memberships. Please contact support@range-pro.com.',
    'GetStoreActivePlans': 'Error: Retrieving store active plans. Please contact support@range-pro.com.',
    'AddStoreMember': 'Error: Adding store member. Please contact support@range-pro.com.',
    'StoreMemberPasswordReset': 'Error: Resetting customer password. Please contact support@range-pro.com.',
    'StoreMemberAccountReset': 'Error: Resetting customer account. Please contact support@range-pro.com.',
    'StoreMemberCardsDelete': 'Error: Removing customer credit card. Please contact support@range-pro.com.',
    'StoreMemberSubscriptionDelete': 'Error: Removing customer subscription. Please contact support@range-pro.com.',
    'StoreMemberCardsAdd': 'Error: Adding customer payment method. Please contact support@range-pro.com.',
    'StoreMemberSubscriptionAdd': 'Error: Adding customer subscription. Please contact support@range-pro.com.',
    'MarkInvoicePaid': 'Error: Marking customer invoice as paid. Please contact support@range-pro.com.',
    'UpdateSubscriptionBilling': 'Error: Updating customer subscription billing. Please contact support@range-pro.com.',
    'AddStoreWaiver': 'Error: Adding store waiver. Please contact support@range-pro.com.',
    'UpdateStoreWaiver': 'Error: Updating store waiver. Please contact support@range-pro.com.',
    'GetStoreWaivers': 'Error: Retrieving store waivers. Please contact support@range-pro.com.',
    'GetStoreWaiverQuestions': 'Error: Retrieving store waiver questions. Please contact support@range-pro.com.',
    'GetWaiverSignatures': 'Error: Retrieving store waiver signatures. Please contact support@range-pro.com.',
    'DeleteStoreWaiver': 'Error: Deleting store waiver. Please contact support@range-pro.com.',
    'AddWaiverQuestion': 'Error: Adding store waiver question. Please contact support@range-pro.com.',
    'EditWaiverQuestion': 'Error: Updating store waiver question. Please contact support@range-pro.com.',
    'DeleteWaiverQuestion': 'Error: Removing store waiver question. Please contact support@range-pro.com.',
    'GetStoreMarketingCustomers': 'Error: Unable to retrieve marketing customers. Please contact support@range-pro.com'
  }
  try {
    store.commit('addNotification', {'message': ERRORS[errorMsg], 'style': 'danger', 'icon': 'error'})
  } catch (err) {
    store.commit('addNotification', {'message': ERRORS['InternalError'], 'style': 'danger', 'icon': 'error'})
    console.log(err)
  }
}

function handleResponse (response, storeResponse, storeInstance, callbackFunction, errorMsg, successMsg) {
  try {
    if (parseInt(response['status']) === 500) {
      processErrorMsg(errorMsg)
      console.log(response)
      callbackFunction(response.data, false)
    } else if (parseInt(response['status']) === 200) {
      if (successMsg) {
        store.commit('addNotification', {'message': successMsg, 'style': 'success', 'icon': 'check'})
      }
      callbackFunction(response.data, true)
    } else {
      try {
        store.commit('addNotification', {
          'message': response.data.error['message'] + ': ' + response.data.error['more_info'],
          'style': 'danger',
          'icon': 'error'
        })
        callbackFunction(response.data, false)
        console.log(response)
      } catch (err) {
        store.commit('addNotification', {
          'message': 'Fatal Error: Unable to connect with RangePro. Please refresh the page.',
          'style': 'danger',
          'icon': 'error'
        })
        callbackFunction(null, false)
        console.log(err)
      }
    }
  } catch (err) {
    store.commit('addNotification', {
      'message': 'Fatal Error: Unable to connect with RangePro. Please refresh the page.',
      'style': 'danger',
      'icon': 'error'
    })
    callbackFunction(null, false)
    console.log(err)
  }
}

function callRangePro (url, method, auth, data, storeResponse, storeInstance, callbackFunction, errorMsg, successMsg, authOverride, fileDownload, fileName) {
  var stage = process.env.API_STAGE
  var baseURL = 'https://api.range-pro.com/v1/'
  var authString = null
  var respType = 'json'
  if (stage === 'testing') {
    baseURL = 'http://127.0.0.1:8080/v1/'
  } else if (stage === 'development') {
    baseURL = 'https://dev-api.range-pro.com/v1/'
  }
  if (auth) {
    authString = store.getters.idToken
    if (authOverride) {
      authString = authOverride
    }
  }
  if (fileDownload) {
    respType = 'blob'
  }
  var errMsg = errorMsg
  var sucMsg = successMsg
  var storeResp = storeResponse
  var storeInst = storeInstance
  var cb = callbackFunction
  var fileDownloadName = fileName
  const instance = axios.create({
    baseURL: baseURL,
    url: url,
    data: data,
    withCredentials: false,
    timeout: 30000,
    method: method,
    responseType: respType,
    headers: {'Authorization': authString, 'content-type': 'application/json'}
  })

  if (fileDownload) {
    instance.request({})
      .then(response => {
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', fileDownloadName)
        document.body.appendChild(link)
        link.click()
      })
      .catch(error => {
        handleResponse(error.response, storeResp, storeInst, cb, errMsg, sucMsg)
      })
  } else {
    instance.request({})
      .then(response => {
        handleResponse(response, storeResp, storeInst, cb, errMsg, sucMsg)
      })
      .catch(error => {
        handleResponse(error.response, storeResp, storeInst, cb, errMsg, sucMsg)
      })
  }
}



