export default {
  state: {
    profile: null,
    email: null,
    profileData: null,
    accessToken: null,
    verification: null,
    isAuthenticated: false,
    accountMessage: false,
    accountMessageText: null,
    accountMessageStyle: null,
    accountMessageIcon: null,
    userMessages: [],
    waiverMode: null,
    waiverSigned: false,
    waiverCustomer: null,
    waiverPhone: null,
    pastInvoices: null,
    count: 0
  },

  getters: {
    profile: state => state.profile,
    email: state => state.email,
    verification: state => state.verification,
    accessToken: state => state.accessToken,
    idToken: state => state.idToken,
    isAuthenticated: state => state.isAuthenticated,
    getNotifications: state => state.userMessages,
    countNotifications: state => state.userMessages.length,
    getProfileData: state => state.profileData,
    hasAccountMessage: state => state.accountMessage,
    getAccountMessageText: state => state.accountMessageText,
    getAccountMessageStyle: state => state.accountMessageStyle,
    getAccountMessageIcon: state => state.accountMessageIcon,
    getWaiverMode: state => state.waiverMode,
    getWaiverSigned: state => state.waiverSigned,
    getWaiverCustomer: state => state.waiverCustomer,
    getWaiverPhone: state => state.waiverPhone,
    getPastInvoices: state => state.pastInvoices
  },

  mutations: {
    setProfile: (state, profile) => {
      state.profile = profile
    },
    setEmail: (state, email) => {
      state.email = email
    },
    setAccessToken: (state, accessToken) => {
      state.accessToken = accessToken
    },
    setIdToken: (state, idToken) => {
      state.idToken = idToken
    },
    setVerification: (state, verification) => {
      state.verification = verification
    },
    setAuthenticated: (state, isAuthenticated) => {
      state.isAuthenticated = isAuthenticated
    },
    resetNotifications: (state) => {
      state.userMessages = []
    },
    addNotification: (state, notificationOptions) => {
      var currentMessages = state.userMessages
      var icon = mapIcons(notificationOptions.icon)
      var styling = mapStyling(notificationOptions.style)
      var message = notificationOptions.message
      var id = Math.floor(Math.random() * 10000)
      if (currentMessages.length > 4) {
        currentMessages.pop()
      }
      state.userMessages = currentMessages.concat([{'id': id, 'message': message, 'styling': styling, 'icon': icon}])
      state.accountMessage = true
      state.accountMessageText = message
      state.accountMessageStyle = notificationOptions.style
      state.accountMessageIcon = icon
    },
    setProfileData: (state, data) => {
      state.profileData = data
    },
    resetAlerts: (state) => {
      state.accountMessage = ''
      state.hasAccountMessage = false
    },
    setWaiverMode: (state, data) => {
      state.waiverMode = data
    },
    setWaiverSigned: (state, data) => {
      state.waiverSigned = data
    },
    setWaiverCustomer: (state, data) => {
      state.waiverCustomer = data
    },
    setWaiverPhone: (state, data) => {
      state.waiverPhone = data
    },
    addPastInvoices: (state, data) => {
      if (state.pastInvoices) {
        if (state.pastInvoices.length > 10) {
          state.pastInvoices.pop()
          state.pastInvoices.push(data)
        } else {
          state.pastInvoices.push(data)
        }
      } else {
        state.pastInvoices = []
        state.pastInvoices.push(data)
      }
    }
  }
}

function mapIcons (icon) {
  // map icons to display in messages
  var options = {
    'user': 'icon-user-follow',
    'error': 'icon-fire',
    'check': 'icon-check'
  }
  return options[icon]
}

function mapStyling (style) {
  // map styling to display in messages
  var options = {
    'success': 'text-success',
    'danger': 'text-danger'
  }
  return options[style]
}



