<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Customers <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <b-button id="btn566" type="submit" size="sm" variant="warning" class="pull-right" @click="downloadExport()"><i class="fa fa-arrow-down"></i> Export Opt-In</b-button>
        <a href="https://docs.range-pro.com/store_account.html#customers" target="new" class="button">
          <b-button id="btn567" type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>

      <h2 class="text-center">Customer List <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"></h2>
      <summary-chart
        ref="customerSummaryChart"
        :chart-data="summary_chart_data"
        :options="{responsive: true, maintainAspectRatio: false}"
        :height="250">
      </summary-chart>

    </b-card>

    <b-card class="bg-white col-md-12">
      <h2 class="text-center">How Customers Hear About You <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"></h2>
      <hab-chart
        ref="customerHearAboutChart"
        :chart-data="hab_chart_data"
        :options="{responsive: true, maintainAspectRatio: false}"
        :height="250">
      </hab-chart>
    </b-card>

    <b-card class="bg-white col-md-12">
      <h2 class="text-center">Age Distribution <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"></h2>
      <age-chart
        ref="customerAgesChart"
        :chart-data="age_chart_data"
        :options="{responsive: true, maintainAspectRatio: false}"
        :height="250">
      </age-chart>
    </b-card>

    <b-card class="bg-white col-md-12">
      <h2 class="text-center">Contact Phone Area Codes <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"></h2>
      <codes-chart
        ref="customerAreaCodesChart"
        :chart-data="codes_chart_data"
        :options="{responsive: true, maintainAspectRatio: false}"
        :height="250">
      </codes-chart>
    </b-card>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import SummaryChart from './CustomerSummaryChart'
  import HabChart from './CustomerHearAboutChart'
  import AgeChart from './CustomerAgeChart'
  import CodesChart from './CustomerAreaCodesChart'

  export default {
    name: 'StoreCustomers',
    components: {
      SummaryChart,
      HabChart,
      AgeChart,
      CodesChart
    },
    data () {
      return {
        loading: true,
        summary_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Customer List Summary',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        hab_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Hear About Sources',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        age_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Age Distribution',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        codes_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Area Codes',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        }
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/marketing/customers/summary', this.handleResponse, 'GetStoreMarketingCustomers')
      },
      downloadExport () {
        rangepro.get_download_secure('store/marketing/customers/export', 'export.csv', this.handleResponse, 'GetStoreMarketingCustomers')
      },
      handleResponse (data, success) {
        if (success) {
          this.summaryChart(data)
          this.habChart(data)
          this.ageChart(data)
          this.codesChart(data)
          this.loading = false
        } else {
          this.loading = false
        }
      },
      summaryChart (data) {
        var tmpDS = {
          label: 'Customer List Summary',
          backgroundColor: '#990000',
          data: data['summary']['data']
        }
        this.summary_chart_data = {
          labels: data['summary']['labels'],
          datasets: [
            tmpDS
          ]
        }
        this.$refs.customerSummaryChart.updateChart()
      },
      habChart (data) {
        var tmpDS = {
          label: 'Hear About Sources',
          backgroundColor: '#262626',
          data: data['hab']['data']
        }
        this.hab_chart_data = {
          labels: data['hab']['labels'],
          datasets: [
            tmpDS
          ]
        }
        this.$refs.customerHearAboutChart.updateChart()
      },
      ageChart (data) {
        var tmpDS = {
          label: 'Age Distribution',
          backgroundColor: '#990000',
          data: data['ages']['data']
        }
        this.age_chart_data = {
          labels: data['ages']['labels'],
          datasets: [
            tmpDS
          ]
        }
        this.$refs.customerAgesChart.updateChart()
      },
      codesChart (data) {
        var tmpDS = {
          label: 'Area Codes',
          backgroundColor: '#262626',
          data: data['locations']['data']
        }
        this.codes_chart_data = {
          labels: data['locations']['labels'],
          datasets: [
            tmpDS
          ]
        }
        this.$refs.customerAreaCodesChart.updateChart()
      },
      getPieChartBackgrounds (arrayLength) {
        var baseChoices = [
          'rgba(65, 184, 131, .8)',
          'rgba(100, 149, 237, .8)',
          'rgba(255, 127, 80, .8)',
          'rgba(220, 20, 60, .8)',
          'rgba(209, 169, 249, .8)',
          'rgba(169, 209, 249, .8)',
          'rgba(200, 13, 107, .8)',
          'rgba(255, 69, 0, .8)',
          'rgba(0, 0, 128, .8)',
          'rgba(152, 251, 152, .8)'
        ]
        var tmpList = []
        for (var i = 0; i <= arrayLength; i++) {
          tmpList.push(baseChoices[Math.floor(Math.random() * baseChoices.length)])
        }
        return tmpList
      }
    }
  }
</script>

<style scoped>

</style>



