<template>
  <div>
    <b-row>
      <b-col cols="2">
        <img class="pull-left" src="@/assets/logo.png" height="40" width="110">
      </b-col>
      <b-col></b-col>
      <b-col cols="2">
        <router-link :to="{ name: 'Kiosk Dashboard', params: { id: kioskID }}"><b-button id="btn61" type="submit" class="btn btn-sm pull-right" variant="warning"><i class="fa fa-home"></i> Home</b-button></router-link>
      </b-col>
    </b-row>
    <div class="mt-5 col-md-12">
      <b-row class="border-gray-200 border p-4" v-if="config.waivers">
        <b-col cols="1" class="align-middle">
          <i class="fa fa-pencil-square-o fa-4x mt-3 text-gray-400"></i>
        </b-col>
        <b-col class="text-left align-middle">
          <h1 class="mt-2"><router-link :to="{ name: 'Waivers Dashboard', params: { id: kioskID }}">Sign Waiver(s)</router-link></h1>
          <p>Sign waivers to utilize all range services</p>
        </b-col>
        <b-col cols="2" class="align-middle">
          <router-link :to="{ name: 'Waivers Dashboard', params: { id: kioskID }}"><i class="fa fa-arrow-circle-right fa-3x text-primary pull-right mt-4"></i></router-link>
        </b-col>
      </b-row>

      <b-row class="border-gray-200 border p-4 mt-1" v-if="config.waitinglist">
        <b-col cols="1" class="align-middle">
          <i class="fa fa-plus-square-o fa-4x mt-3 text-gray-400"></i>
        </b-col>
        <b-col class="text-left align-middle">
          <h1 class="mt-2"><router-link :to="{ name: 'WaitingList Dashboard', params: { id: kioskID }}">Add to the Waiting List</router-link></h1>
          <p>Add yourself to the range waiting list</p>
        </b-col>
        <b-col cols="2" class="align-middle">
          <router-link :to="{ name: 'WaitingList Dashboard', params: { id: kioskID }}"><i class="fa fa-arrow-circle-right fa-3x text-primary pull-right mt-4"></i></router-link>
        </b-col>
      </b-row>

      <b-row class="border-gray-200 border p-4 mt-1" v-if="config.selfservice">
        <b-col cols="1" class="align-middle">
          <i class="fa fa-group fa-4x mt-3 text-gray-400"></i>
        </b-col>
        <b-col class="text-left align-middle">
          <h1 class="mt-2"><router-link :to="{ name: 'SelfService Dashboard', params: { id: kioskID }}">Start Here</router-link></h1>
          <p>Add yourself to the range and/or waiting list</p>
        </b-col>
        <b-col cols="2" class="align-middle">
          <router-link :to="{ name: 'SelfService Dashboard', params: { id: kioskID }}"><i class="fa fa-arrow-circle-right fa-3x text-primary pull-right mt-4"></i></router-link>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
    import store from '@/store'
    import rangepro from '@/services/rangepro'

    export default {
      name: 'Dashboard',
      data () {
        return {
          kioskID: null,
          config: null
        }
      },
      mounted: function () {
        this.kioskID = this.$route.params.id
        this.getData()
        // Reset waiver state for Self-Service
        store.commit('setWaiverMode', null)
        store.commit('setWaiverSigned', false)
        store.commit('setWaiverCustomer', null)
        store.commit('setWaiverPhone', null)
      },
      methods: {
        getData () {
          rangepro.get_secure('store/kiosk/' + this.kioskID, this.handleResponse, 'GetStoreKiosks')
        },
        handleResponse (data, success) {
          if (success) {
            this.config = data.kiosk
            this.loading = false
          } else {
            this.loading = false
          }
        }
      }
    }
</script>

<style scoped>

</style>



