<template>
  <b-col cols="12" md="12">
    <div>
      <b-alert :show="dismissCountDown"
               dismissible
               :variant="messageStyle"
               @dismissed="dismissCountdown=0"
               @dismiss-count-down="countDownChanged">
        <i :class="messageIcon" class="mr-2"></i>{{ messageText }}
        <b-progress :variant="messageStyle"
                    :max="dismissSecs"
                    :value="dismissCountDown"
                    height="4px">
        </b-progress>
      </b-alert>
    </div>

    <b-modal ref="alertModal" id="alertModal" hide-footer :title="messageTitle" class="modal-dark text-light">
      <div class="d-block text-left text-dark">
        <p><i :class="messageIcon" class="text-danger mr-1"></i>{{ messageText }}</p>
      </div>
      <b-btn id="btn901" class="mt-3" variant="outline-success" block @click="$refs.alertModal.hide()">Ok</b-btn>
    </b-modal>

  </b-col>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'

  export default {
    name: 'alerts',
    data () {
      return {
        dismissSecs: 5,
        dismissCountDown: 0,
        showDismissibleAlert: false,
        hasMessage: false,
        messageText: '',
        messageTitle: 'Success'
      }
    },
    methods: {
      countDownChanged (dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert () {
        this.dismissCountDown = this.dismissSecs
      },
      getAlert () {
        if (this.hasAccountMessage) {
          this.messageText = this.getAccountMessageText
          this.messageStyle = this.getAccountMessageStyle
          this.messageIcon = this.getAccountMessageIcon
          if (this.messageStyle === 'danger') {
            this.messageTitle = this.messageText.split(':')[0]
            this.messageText = this.messageText.split(':')[1]
            this.$refs.alertModal.show()
          }
          this.showAlert()
          this.resetAlerts()
        }
      },
      ...mapMutations([
        'resetAlerts'
      ])
    },
    computed: {
      ...mapGetters([
        'hasAccountMessage',
        'getAccountMessageText',
        'getAccountMessageStyle',
        'getAccountMessageIcon'
      ])
    },
    mounted: function () {
      this.getAlert()

      setInterval(function () {
        this.getAlert()
      }.bind(this), 2000)
    }
  }
</script>



